import React, { useState } from "react";
import { Row, Col, Form, Button, Card } from "react-bootstrap";
import styles from "./CustomCard.module.css";
import { Switch, Route, useRouteMatch, useHistory } from "react-router-dom";
import Users from "./Users";

const RouteChange = () => {
    const [route, setRoute] = useState("");
    const { url } = useRouteMatch();
    let history = useHistory();
    const handleOnSubmit = (event) => {
        event.preventDefault();
        setRoute(route);
        history.push(`./${route}`);
    };
    return (
        <Switch>
            <Route path={`${url}/users`}>
                <Users />
            </Route>
            <Route path={`${url}/`}>
                <Row className="justify-content-center">
                    <Col sm={5} className="mt-3">
                        <Card
                            className={["border-warning", styles.myCard].join(
                                " "
                            )}
                        >
                            <Card.Header
                                className={[
                                    "border-warning bg-dark text-white text-center font-weight-bold",
                                    styles.cardHeader,
                                ].join(" ")}
                            >
                                Change Route
                            </Card.Header>
                            <Card.Body>
                                <Form onSubmit={handleOnSubmit}>
                                    <Form.Group controlId="formBasicEmail">
                                        <Form.Label>Route</Form.Label>
                                        <Form.Control
                                            type="text"
                                            onChange={(event) =>
                                                setRoute(event.target.value)
                                            }
                                            placeholder="Enter route"
                                        />
                                        <Form.Text className="text-muted">
                                            Enter route to change route.
                                        </Form.Text>
                                    </Form.Group>
                                    <Button
                                        variant="outline-primary"
                                        type="submit"
                                        className={styles.customButton}
                                    >
                                        Submit
                                    </Button>
                                </Form>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Route>

            <Route path="*">
                <h1 className="text-center">404 Route s{route} Not Found</h1>
            </Route>
        </Switch>
    );
};

export default RouteChange;
