import React, { useState, useEffect } from "react";
import { Container, Col, Row } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import styles from "./CustomCard.module.css";
import UserList from "./UserList";
import withLoader from "./withLoader";
const UsersListWithLoader = withLoader(UserList);

function Users(props) {
    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [users, setUsers] = useState([]);
    useEffect(() => {
        fetch("https://5e4cbba99b6805001438ed50.mockapi.io/users")
            .then((res) => res.json())
            .then(
                (result) => {
                    setIsLoaded(true);
                    setUsers(result);
                },
                (error) => {
                    setIsLoaded(true);
                    setError(error);
                }
            );
    }, [props]);

    if (error) {
        return <div>Error: {error.message}</div>;
    } else {
        return (
            <React.Fragment>
                <Container>
                    <Row className="justify-content-center">
                        <UsersListWithLoader
                            isLoaded={isLoaded}
                            users={users}
                        />
                        <Col className="mt-3" lg={6}>
                            <UrlQuery />
                        </Col>
                    </Row>
                </Container>
            </React.Fragment>
        );
    }
}

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function UrlQuery() {
    const QueryParams = useQuery();
    return (
        <div className={["card border-warning", styles.myCard].join(" ")}>
            <div
                className={[
                    "card-header bg-dark text-white text-center",
                    styles.cardHeader,
                ].join(" ")}
            >
                <b>URL Query</b>
            </div>
            <ul className="list-group list-group-flush">
                <li
                    className={["list-group-item", styles.listGroupItem].join(
                        " "
                    )}
                >
                    <label className="col-12 col-sm-6 text-left pl-3">
                        <b>pageno : {QueryParams.get("pageno") || 1} </b>
                    </label>
                </li>
                <li
                    className={["list-group-item", styles.listGroupItem].join(
                        " "
                    )}
                >
                    <label className="col-12 col-sm-6 text-left pl-3">
                        <b>Sort : {QueryParams.get("sort") || "id"} </b>
                    </label>
                </li>
                <li
                    className={["list-group-item", styles.listGroupItem].join(
                        " "
                    )}
                >
                    <label className="col-12 col-sm-6 text-left pl-3">
                        <b>page size : {QueryParams.get("pagesize") || 10} </b>
                    </label>
                </li>
            </ul>
        </div>
    );
}

export default Users;
