import React from "react";
import { Col, Button } from "react-bootstrap";
import { Link, useRouteMatch } from "react-router-dom";
import styles from "./CustomCard.module.css";

function PostList(props) {
    const { path, url } = useRouteMatch();
    return (
        <Col className="mt-3" lg={6}>
            <div className={["card border-warning", styles.myCard].join(" ")}>
                <div
                    className={[
                        "card-header bg-dark text-white text-center",
                        styles.cardHeader,
                    ].join(" ")}
                >
                    <b>Posts</b>
                </div>
                <ul className="list-group list-group-flush">
                    {props.posts.map((Post, index) => (
                        <li
                            key={Post.id}
                            className={[
                                "list-group-item",
                                styles.listGroupItem,
                            ].join(" ")}
                        >
                            <div className="row">
                                <label className="col-12 col-sm-6 text-center text-sm-left pl-3">
                                    <b>
                                        Post {Post.id}: {Post.title}
                                    </b>
                                </label>
                                <Link
                                    to={`${path}/${Post.id}`}
                                    className="col-12 col-sm-6 text-white text-decoration-none text-center text-sm-right"
                                >
                                    <Button
                                        variant="outline-primary"
                                        className={styles.customButton}
                                    >
                                        View Post
                                    </Button>
                                </Link>
                            </div>
                        </li>
                    ))}
                    <li
                        className={[
                            "list-group-item",
                            styles.listGroupItem,
                        ].join(" ")}
                    >
                        <div className="row justify-content-center">
                            <Link
                                to={`${url}/childcomponent`}
                                className="text-white text-decoration-none"
                            >
                                <Button
                                    variant="outline-primary"
                                    className={styles.roundedButton}
                                >
                                    Child Component
                                </Button>
                            </Link>
                        </div>
                    </li>
                </ul>
            </div>
        </Col>
    );
}

export default PostList;
